        console.log(result);
        data.value = result.data;
      });
    };

    const addToCart = function (prodcutInfo) {
      postProduct(prodcutInfo);
    };

    const createModal = () => {
      /* detailModalDom.value.detailModal
      = new Modal(document.querySelector('#detailModal')).show() */
    };

    return {
      data,
      detailModalDom,
      onChangePage,
      addToCart,
      createModal,
    };
  },
};
</script>
